import createClient from 'openapi-fetch';
import semver from 'semver';
import * as core from '@actions/core';
// eslint-disable-next-line import/no-relative-packages
import pkg from '../../package.json';
export async function getClient(context) {
    const { baseUrl } = context;
    validateCIActionVersion(baseUrl);
    const token = await core.getIDToken();
    const client = createClient({ baseUrl });
    client.use({
        async onRequest(req, _options) {
            req.headers.set('Authorization', token);
            req.headers.set('User-Agent', getUserAgent(context));
            return req;
        },
    });
    return client;
}
export function getUserAgent(context) {
    const version = getVersion();
    return `PlatformGithubAction/${version} (${formatContext(context)})`;
}
export function formatContext(context) {
    const resultParts = Object.entries(context).map(([key, value]) => `${key}=${value}`);
    return resultParts.join('; ');
}
export function getVersion() {
    return pkg.version;
}
async function validateCIActionVersion(baseUrl) {
    const resp = await fetch(`${baseUrl}/discovery`);
    const { minimum_ci_action_version: minVersion } = await resp.json();
    if (semver.lt(getVersion(), minVersion)) {
        throw new Error(`Minimum CI Action version ${minVersion} required, found ${getVersion()}`);
    }
}
