import * as github from '@actions/github';
export function getContext() {
    if (!github.context.payload.repository) {
        throw new Error('No repository found in context');
    }
    if (!github.context.payload.repository.full_name) {
        throw new Error('No full_name found in repository in context');
    }
    let headRef;
    if (github.context.eventName === 'pull_request') {
        headRef = github.context.payload.pull_request?.head.ref;
    }
    else if (github.context.eventName === 'push') {
        // TODO(elopez): this won't work with tags
        const pruneHead = 'refs/heads/';
        headRef = github.context.payload.ref.substring(pruneHead.length);
    }
    else if (github.context.eventName === 'release') {
        headRef = github.context.payload.repository.default_branch;
    }
    else {
        throw new Error(`EventName ${github.context.eventName} not supported`);
    }
    if (!headRef) {
        throw new Error('No head ref found in context');
    }
    return {
        eventName: github.context.eventName,
        runId: github.context.runId,
        job: github.context.job,
        step: github.context.action,
        repo: github.context.payload.repository.full_name,
        branch: headRef,
        baseUrl: process.env.PLATFORM_BASE_URL || 'https://core-platform-staging.core-platform.dev.czi.team',
    };
}
